:root {
  --primary: #6366f1;
  --primary-hover: #4f46e5;
  --bg: #f8fafc;
  --card-bg: #ffffff;
  --text: #334155;
  --border: #e2e8f0;
}

* { box-sizing: border-box; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  background-color: var(--bg);
  color: var(--text);
  padding: 20px;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

/* Header */
.header {
  text-align: center;
  margin-bottom: 30px;
  margin-top: 20px;
}

.logo-area {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: var(--primary);
}

.header h1 { margin: 0; color: #1e293b; font-size: 1.8rem; }
.header p { color: #64748b; margin-top: 5px; }

/* Main Layout */
.main-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Card */
.card {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05);
  border: 1px solid var(--border);
}

/* --- TOOLBAR & VOICE SELECTOR UPDATES --- */
.toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  gap: 15px; /* Increased gap slightly */
}

/* Wrapper to hold Select + "Speaking" text */
.voice-selector-wrapper {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 12px;
}

.voice-select {
  flex: 1; /* Takes available space inside the wrapper */
  padding: 12px;
  border-radius: 8px;
  border: 1px solid var(--border);
  background: white;
  font-size: 14px;
  color: var(--text);
  cursor: pointer;
  appearance: none; 
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 16px;
  min-width: 0; /* Prevents overflow in flexbox */
}

.voice-select:focus {
  outline: 2px solid var(--primary);
  border-color: transparent;
}

/* The "Speaking..." Indicator */
.playing-indicator {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 0.85rem;
  color: var(--primary);
  font-weight: 600;
  white-space: nowrap;
  animation: pulse 1.5s infinite ease-in-out;
}

@keyframes pulse {
  0% { opacity: 0.5; }
  50% { opacity: 1; }
  100% { opacity: 0.5; }
}

.badge {
  display: flex;
  align-items: center;
  gap: 6px;
  background: #f1f5f9;
  padding: 8px 12px;
  border-radius: 20px;
  font-size: 12px;
  color: #64748b;
  font-weight: 600;
  white-space: nowrap;
}

/* --- END TOOLBAR UPDATES --- */

.script-input {
  width: 100%;
  height: 250px;
  padding: 15px;
  border: 1px solid var(--border);
  border-radius: 10px;
  resize: vertical;
  font-size: 16px; 
  line-height: 1.6;
  margin-bottom: 20px;
  background-color: #fbfbfc;
  font-family: inherit;
}

.script-input:focus {
  outline: 2px solid var(--primary);
  border-color: transparent;
}

.action-btn {
  width: 100%;
  padding: 16px;
  background-color: var(--primary);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, transform 0.1s;
}

.action-btn:hover {
  background-color: var(--primary-hover);
}

.action-btn:active {
  transform: scale(0.99);
}

.action-btn:disabled {
  background-color: #cbd5e1;
  cursor: not-allowed;
  transform: none;
}

/* Smooth Progress Bar */
.progress-container {
  margin-top: 10px;
}

.progress-info {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  color: #64748b;
  margin-bottom: 8px;
  font-weight: 500;
}

.progress-bar-bg {
  width: 100%;
  height: 8px;
  background-color: #e2e8f0;
  border-radius: 4px;
  overflow: hidden;
}

.progress-bar-fill {
  height: 100%;
  background-color: var(--primary);
  transition: width 0.2s linear; 
}

/* Result Area */
.result-card {
  text-align: center;
  border: 1px solid #e0e7ff; 
}

.success-icon {
  color: #10b981;
  margin-bottom: 15px;
}

.audio-wrapper {
  margin: 20px 0;
}

.main-audio {
  width: 100%;
  height: 40px;
}

.meta-stats {
  font-size: 0.85rem;
  color: #64748b;
  margin-bottom: 20px;
}

.download-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  background-color: #10b981;
  color: white;
  text-decoration: none;
  padding: 14px 24px;
  border-radius: 10px;
  font-weight: bold;
  width: 100%;
  transition: background 0.2s;
}

.download-btn:hover {
  background-color: #059669;
}

/* Animations */
.fade-in {
  animation: fadeIn 0.6s cubic-bezier(0.22, 1, 0.36, 1);
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Mobile Breakpoints */
@media (max-width: 600px) {
  .container { padding: 10px; }
  .card { padding: 15px; }
  .header h1 { font-size: 1.5rem; }
  .script-input { height: 200px; }
  
  /* Stack toolbar items on very small screens */
  .toolbar {
    flex-direction: column; 
    align-items: stretch;
  }
  .voice-selector-wrapper { width: 100%; }
  .badge { align-self: flex-start; }
}